import React, { FC } from 'react';

import SectionTitle from './section-title';

interface Props {
  data: {
    title: string;
    message: string;
  };
}

const Greeting: FC<Props> = ({ data }) => (
  <section>
    <SectionTitle>{data.title}</SectionTitle>
    <p className="text-justify">{data.message}</p>
  </section>
);

export default Greeting;
