import React, { FC } from 'react';

import Check from '@fortawesome/fontawesome-free/svgs/solid/check-circle.svg';

import SectionTitle from './section-title';

interface Props {
  data: Array<{
    name: string;
    background: string;
    logo: string;
    description: string;
    keywords: string[];
  }>;
}

const Projects: FC<Props> = ({ data }) => (
  <section>
    <SectionTitle>Projects</SectionTitle>
    <ul>
      {data.map((project) => (
        <li key={project.name} className="mx-4 my-2">
          <div className="h-6 flex">
            <Check className="h-full text-green-600" />
            <span className="flex-1 ml-4">{project.name}</span>
          </div>
          <div className="ml-10 text-sm italic font-medium text-gray-600">{project.keywords.join(', ')}</div>
        </li>
      ))}
    </ul>
  </section>
);

export default Projects;
